async function GetMessages(project) {

    console.log(project);
  
    const req = await fetch('http://localhost:1337/api/get-messages', {
      headers: {
        'project': project,
      },
    });
  
    const data = await req.json();
    if (data.status === 'ok') {
      return data.message;
    } else {
      throw new Error(data.error);
    }
  }


  async function DeleteMessage(message) {

    const response = await fetch('http://localhost:1337/api/delete-message', {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
    },
      body: JSON.stringify({
        message,
      }),
    })
  
    const data = await response.json() 
  
    if (data.status === 'ok') {
      console.log("message deleted from the database")
    }
  }

  export { GetMessages, DeleteMessage }