import React, { useEffect, useState, useRef } from 'react'
import jwt from 'jsonwebtoken'
import { useNavigate } from 'react-router-dom'
import Naviguation from "../components/Naviguation";
import { NavLink } from "react-router-dom";
import Projects from '../components/Projects';
import './../stylesheets/projects.css'
import { GetProjectData, DeleteProject} from "./../api/getprojects"
import { GetUserData } from "./../api/getusers"
import { DeleteChat } from "./../api/chat"
import Profil from '../components/Profil';

const Dashboard = () => {
	const history = useNavigate()
	const [username, setUsername] = useState('')
	const [surname, setSurname] = useState('')
	const [name, setName] = useState('')
    const [projectData, setProjectData] = useState([])
	const avatarRef = useRef(null);
	const [token, setToken] = useState(localStorage.getItem('token'))

	useEffect(() => { 
		if (token) {
			const user = jwt.decode(token)
			if (!user) {
				localStorage.removeItem('token')
				history('/login')
			} else {
				GetUserData()
					.then(data => (setUsername(data.username),setSurname(data.surname), setName(data.name) ,localStorage.setItem('surname', data.surname), localStorage.setItem('name', data.name)))
					.catch(error => alert(error.message))

			}
		}
		else {
			history('/login')
		}

	}, [token])

	useEffect(() => {

		setSurname(localStorage.getItem('surname'))
        setName(localStorage.getItem('name'))

		if (surname && name) {
		  GetProjectData(surname, name)
			.then(data => setProjectData(data))
			.catch(error => alert(error.message));
		}
	  }, [surname, name]);

	const handleProject = (projectName, projectCategory, projectCreator, projectParticipants) => {
		localStorage.setItem('project', projectName)
		const participants = JSON.stringify(projectParticipants);
		localStorage.setItem('participants', participants);
  		history(`/project/${projectCreator}/${projectCategory}/${projectName}`);
	}

	const handleDelete = (projectName) => {
		const shouldDelete = window.confirm('Are you sure you want to delete this message?');

		if (shouldDelete) {
			DeleteChat(projectName)
			DeleteProject(projectName)
			window.location.reload(false);
		}
	}

	const HandleLogOut = () => {
		localStorage.clear();
		window.location.reload(false);
	}
	
	return (
		<div>
			<div className="dashboard-nav-bar">
				<NavLink to="/" className={(nav) => (nav.isActive ? "nav-active" : "")}>
                <img src="img/logo.png" alt="logo" />
        		</NavLink>
				<div className="dashboard-naviguation">
					<Naviguation />
				</div>
				<Profil HandleLogOut={() =>HandleLogOut()} forwardedRef={avatarRef}/>
			</div>
			<h1>{username} Dashboard </h1>
		<div className="projects">
			<div className="intro">
				<h2>Projects</h2>
				<NavLink to="/new-project" className={(nav) => (nav.isActive ? "nav-active" : "")}>
          		<li><button>New Project</button></li>
        		</NavLink>
			</div>
			<div className="projects-data">
				{
				projectData.map((project, index) => (
					<Projects key={index} name={project.name} category={project.category} creator={project.creator} participants={project.participants} handleProject={() => handleProject(project.name, project.category, project.creator, project.participants)} handleDelete={() => handleDelete(project.name)}/>
					))
				}
			</div>
		</div>
		
		</div>
	)
}

export default Dashboard