import React from 'react';

const Projects = ({name, category, creator, participants, handleProject, handleDelete}) => {
    return (
        <div className='project'>
            <div className="project-info">
                <div className="delete-project-button">
                    <h3>{name}</h3>
                    <button onClick={handleDelete}><img src="/img/poubelle.png" alt="" /></button></div>
                <li>createur : {creator}</li>
                <li>categorie : {category}</li>
                <li> <p>Participants :</p>
                {
                    participants.map((participant, index) => (
                        <p key={index}>{participant},</p>
                    ))
                }
                </li>
                <button onClick={handleProject}>enter project</button>
            </div>
        </div>
    );
};

export default Projects;