async function GetProjectData(surname, name) {
  const creator = surname + " " + name;

  const req = await fetch('http://localhost:1337/api/project-data', {
    headers: {
      'creator': creator,
    },
  });

  const data = await req.json();
  if (data.status === 'ok') {
    return data.project;
  } else {
    throw new Error(data.error);
  }
}

async function DeleteProject(projectName) {

  const response = await fetch('http://localhost:1337/api/delete-project', {
    method: 'DELETE',
    headers: {
      'Content-Type': 'application/json',
  },
    body: JSON.stringify({
      projectName,
    }),
  })

  const data = await response.json() 

  if (data.status === 'ok') {
    console.log("project deleted from the database")
  }
}

export { GetProjectData, DeleteProject };
