import React from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import Login from './pages/Login'
import Register from './pages/Register'
import Dashboard from './pages/Dashboard'
import Accueil from'./pages/Accueil'
import NewProject from './pages/NewProject'
import Project from './pages/Project'
import Profil from './pages/Profil'
import Chat from './pages/Chat/Chat'
import Projects from './pages/accueil/Projects'
import JoinUs from './pages/accueil/JoinUs'
import FinanceUs from './pages/accueil/FinanceUs'
import Partenaires from './pages/accueil/Partenaires'

const App = () => {
	return (
		<div>
			<BrowserRouter>
				<Routes> 
					<Route path='/' element={<Accueil />} />
					<Route path="/login" element={<Login />} />
					<Route path="/register" element={<Register />} />
					<Route path="/dashboard" element={<Dashboard />} />
					<Route path="/dashboard/profil" element={<Profil />} />
					<Route path="/new-project" element={<NewProject />} />
					<Route path="/project/:projectCreator/:projectCategory/:projectName" element={<Project />} />
					<Route path="/chat/project/:projectCreator/:projectCategory/:projectName" element={<Chat />} />
					<Route path='/projects' element={<Projects />} />
					<Route path='/join-us' element={<JoinUs />} />
					<Route path='/finance-us' element={<FinanceUs />} />
					<Route path='/partenaires' element={<Partenaires />} />
				</Routes>
			</BrowserRouter>
		</div>
	)
}

export default App