async function DeleteChat(projectName) {

    const response = await fetch('http://localhost:1337/api/delete-chat', {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
    },
      body: JSON.stringify({
        projectName,
      }),
    })
  
    const data = await response.json() 
  
    if (data.status === 'ok') {
      console.log("chat deleted from the database")
    }
  }

  export { DeleteChat };