import React, { useState, useEffect, useRef } from 'react';
import Naviguation from "../../components/Naviguation";
import { GetMessages, DeleteMessage} from "./../../api/message";
import io from 'socket.io-client';
import './chat.css';
import { NavLink } from 'react-router-dom';

const socket = io('http://localhost:5000');

const Chat = () => {
  const [surname, setSurname] = useState('');
  const [name, setName] = useState('');
  const [sender, setSender] = useState('');
  const [projectName, setProjectName] = useState('');
  const [participants, setParticipants] = useState([])
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const [onlineStatus, setOnlineStatus] = useState(false)
  const [statusColor, setStatusColor] = useState('red')
  const chatMessagesRef = useRef(null);

  useEffect(() => {
    socket.on('connect', () => {
      console.log(socket.id);
    });
    socket.on('receive-message', (message) => {
      console.log(message);
      setMessages((messages) => [...messages, message]);
    });
    socket.on("disconnect", () => {
      setStatusColor('red')
    });
  }, []);

  useEffect(() => {
    if (projectName) {
      socket.emit('join-room', projectName)
    }
  }, [projectName])

  useEffect(() => {
    const project = localStorage.getItem('project');
    const participantsString = localStorage.getItem('participants');
    const participants = JSON.parse(participantsString || '[]');
    setProjectName(project);
    setParticipants(participants)
    setSurname(localStorage.getItem('surname'));
    setName(localStorage.getItem('name'));
    setStatusColor('green')
  }, []);

  useEffect(() => {
    setSender(`${surname} ${name}`);

    if (projectName) {
      GetMessages(projectName)
        .then((data) => setMessages(data))
        .catch((error) => alert(error.message));
    }
  }, [projectName, surname, name]);

  async function createMessage() {
    const sender = `${surname} ${name}`;

    const response = await fetch('http://localhost:1337/api/new-message', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        projectName,
        sender,
        message,
      }),
    });

    const data = await response.json();

    if (data.status === 'ok') {
      console.log('chat created');
    }
  }

  const sendMessage = (e) => {

    e.preventDefault();
    if (message) {
      const newMessage = {
        sender: sender,
        message: message,
      };
      socket.emit('send_message', newMessage, projectName);
      setMessage('');
      createMessage();

      setMessages((messages) => [...messages, newMessage]);
    }
  };

  const deleteMessage = (message) => {
    const shouldDelete = window.confirm('Are you sure you want to delete this message?');
  
    if (shouldDelete) {
      DeleteMessage(message);
      setMessages([message, ...messages]);
      window.location.reload(false);
    }
  }

  useEffect(() => {
    if (chatMessagesRef.current) {
      chatMessagesRef.current.scrollTop = chatMessagesRef.current.scrollHeight;
    }
  }, [messages]);

  return (
    <div className="chat-container">
      <NavLink to="/" className={(nav) => (nav.isActive ? "nav-active" : "")}>
                <img src="../../../../img/logo-dark.png" alt="logo" />
        		</NavLink>
				<div className="dashboard-naviguation">
					<Naviguation />
				</div>
      <p>{projectName}</p>
      <div className="messages-online-users-container">
      <div className="chat-messages" ref={chatMessagesRef}>
        {messages.map((message, index) => (
          <div key={index} className="chat-message">
            <span className="chat-author">{message.sender} : <button onClick={() => deleteMessage(message)}><img src="/img/poubelle.png" alt="" /></button></span>
            <span className="chat-content">{message.message}</span>
          </div>
        ))}
      </div>
      <div className="online-users">
        {
        participants.map((user, index) => (
          <div className="user">
            <p key={index}>{user}</p>
            <div className="online-status" style={{
            backgroundColor: user === sender ? statusColor : 'red', }}>
            </div>
          </div>
        ))
        }
        </div>
      </div>
      <form className="chat-form" onSubmit={sendMessage}>
        <input
          className="chat-input"
          type="text"
          placeholder="Type your message..."
          value={message}
          onChange={(e) => setMessage(e.target.value)}
        />
        <button className="chat-button" type="submit">
          Send
        </button>
        </form>
    </div>
  );
};

export default Chat;
