import React, { useEffect, useState } from 'react';
import { useParams, Link, useNavigate, NavLink } from 'react-router-dom';
import Naviguation from './../components/Naviguation';
import { GetProjectData } from './../api/getprojects';

const ProjectPage = () => {
  const { projectName, projectCategory, projectCreator } = useParams();
  const [projectData, setProjectData] = useState([]);
  const [surname, setSurname] = useState('');
  const [name, setName] = useState('');
  const history = useNavigate()

  useEffect(() => {
    setSurname(localStorage.getItem('surname'));
    setName(localStorage.getItem('name'));

    if (surname && name) {
      GetProjectData(surname, name)
        .then(data => setProjectData(data))
        .catch(error => alert(error.message));
    }
  }, [surname, name]);

  const handleProject = (projectName, projectCategory, projectCreator) => {
    history(`/project/${projectCreator}/${projectCategory}/${projectName}`);
  };

  const handleChat = () => {
    history(`/chat/project/${projectCreator}/${projectCategory}/${projectName}`);
  };

  return (
    <div>
        <NavLink to="/" className={(nav) => (nav.isActive ? "nav-active" : "")}>
                <img src="img/logo-dark.png" alt="logo" />
        		</NavLink>
				<div className="dashboard-naviguation">
					<Naviguation />
				</div>
      <h1>{projectName}</h1>
      <button onClick={handleChat}>Chat</button>
    </div>
  );
};

export default ProjectPage;
