import React, { useEffect, useState } from 'react';
import jwt from 'jsonwebtoken'
import { useNavigate } from 'react-router-dom'
import Naviguation from "../components/Naviguation";
import { getAllUsers } from "./../api/getusers"

const NewProject = () => {

    const [projectName, setProjectName] = useState('')
	const [projectCategory, setProjectCategory] = useState('');
    const [projectCreator, setProjectCreator] = useState('')
	const [token, setToken] = useState(localStorage.getItem('token'))
	const [fullNames, setFullNames] = useState([])
	const [selectedParticipants, setSelectedParticipants] = useState([]);
  	const [isParticipantListVisible, setIsParticipantListVisible] = useState(false);

	const history = useNavigate()

	useEffect(() => { 
		if (token) {
			const user = jwt.decode(token)
			if (!user) {
				localStorage.removeItem('token')
				history('/login')
			} else {
				getAllUsers()
				.then(data => {
					const newFullNames = data.map(obj => `${obj.surname} ${obj.name}`);
					setFullNames(newFullNames);
				  })
					.catch(error => alert(error.message))


			}
		}
		else {
			history('/login')
		}

	}, [token])

	async function createChat(event) {
		event.preventDefault()

		const response = await fetch('http://localhost:1337/api/new-chat', {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: JSON.stringify({
				projectName,
				selectedParticipants, 
			}),
		})

		const data = await response.json() 

		if (data.status === 'ok') {
			console.log('chat created');
		}
	}

    async function createProject(event) {
		event.preventDefault()

		const response = await fetch('http://localhost:1337/api/new-project', {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: JSON.stringify({
				projectName,
				projectCategory,
				projectCreator, 
				selectedParticipants,
			}),
		})

		const data = await response.json() 

		if (data.status === 'ok') {
			history('/dashboard')
		}
	}

	useEffect(() => { 
		const surname = localStorage.getItem('surname')
		const name = localStorage.getItem('name')
		setProjectCreator(surname + " " + name)
		
	}, [])

	const handleSubmit = (event) => {
		createProject(event)
		createChat(event)
	}

	const handleParticipantSearch = (e) => {
		// Perform any filtering or search functionality here if needed
		setIsParticipantListVisible(true);
	  };
	
	  const handleParticipantSelect = (participant) => {
		if (!selectedParticipants.includes(participant)) {
			const updatedParticipants = [...selectedParticipants, participant];
			setSelectedParticipants(updatedParticipants);
			setIsParticipantListVisible(false);
		  }
	  };
	
	  const handleParticipantRemove = (participant) => {
		const updatedParticipants = selectedParticipants.filter(p => p !== participant);
		setSelectedParticipants(updatedParticipants);
		setIsParticipantListVisible(false);
	  };

    return (
        <div>
			<Naviguation />
            <div className="form">
			<form onSubmit={handleSubmit}>
			<h1>New Project</h1>
			<input
					value={projectName}
					onChange={(e) => setProjectName(e.target.value)}
					type="text"
					placeholder="Project name"
				/>
				<br />
				<div className="participant-input-container">
          		<div className="selected-participants-input">
            		{selectedParticipants.map((participant, index) => (
              		<div key={index} className="selected-participant">
                		{participant}
                		<button type="button" onClick={() => handleParticipantRemove(participant)}><img src='img/cross.png'></img></button>
              		</div>
            		))}
            		<input
              		value=""
              		onChange={handleParticipantSearch}
              		type="text"
              		placeholder="Participants"
              		onFocus={() => setIsParticipantListVisible(true)}
            		/>
          		</div>
          		{isParticipantListVisible && (
            		<ul className="participant-list" style={{ display: isParticipantListVisible ? 'flex' : 'none' }}>
              		{fullNames.map((participant, index) => (
                		<li key={index} onClick={() => handleParticipantSelect(participant)}>
                  		{participant}
                		</li>
              		))}
            		</ul>
          		)}
        		</div>
				<select value={projectCategory} onChange={(e) => setProjectCategory(e.target.value)}>
					<option value="">--Select a Category--</option>
					<option value="Entrepreuneuriat">Entrepreuneuriat</option>
					<option value="Economie-finance">Economie-finance</option>
					<option value="Robotique">Robotique</option>
					<option value="Programmation">Programmation</option>
				</select>
				<input
					value={projectCreator}
					type="text"
					readOnly 
				/>

				<input type="submit" value="Créer le projet" />
			</form>
			</div>
        </div>
    );
};

export default NewProject;